import React, { Component } from 'react';
import Navitem from './NavItem';
class Navbar extends Component {
    constructor(props){
        super(props);
        this.state={
        'NavItemActive':''
        }
    }
    activeitem=(x)=>{
        if(this.state.NavItemActive.length>0){
            document.getElementById(this.state.NavItemActive).classList.remove('active');
        }
        this.setState({'NavItemId':x},()=>{
        document.getElementById(this.state.NavItemActive).classList.add('active');
        });
    };
    render() {
        return (
            <nav className="site-nav">
                <ul>
                <Navitem item="Home" tolink="/" className="home"></Navitem>
                <Navitem item="About" tolink="/about" className="about"></Navitem>
                <Navitem item="Education" tolink="/education" className="education"></Navitem>
                <Navitem item="Skills" tolink="/skills" className="skills"></Navitem>
                <Navitem item="Projects" tolink="/projects" className="projects"></Navitem>
                <Navitem item="Contact" tolink="/contact" className="contact"></Navitem>
                </ul>
            </nav>
        )
    }
}
export default Navbar