import React, { Component } from 'react';
import 'font-awesome/css/font-awesome.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebookSquare,faGithubSquare,faInstagramSquare,faLinkedin,faLinkedinIn,faTwitterSquare } from '@fortawesome/free-brands-svg-icons';
class Social extends Component {
    render() {
        return (
            <div className="social">
                <a href="https://github.com/AbdulBasit-MrRobo" target="_blank"><span className="icon-class"><FontAwesomeIcon icon={ faGithubSquare }/></span></a>
                <a href="https://www.instagram.com/abdul_basit_h_16/" target="_blank"><span className="icon-class"><FontAwesomeIcon icon={ faInstagramSquare }/></span></a>
                <a href="https://www.facebook.com/basit.hakimi/" target="_blank"><span className="icon-class"><FontAwesomeIcon icon={ faFacebookSquare }/></span></a>
                <a href="https://www.linkedin.com/in/abdulbasit-hakimi-622529186/" target="_blank"><span className="icon-class"><FontAwesomeIcon icon={ faLinkedinIn }/></span></a>
                <a href="https://twitter.com/Mr__Robo__" target="_blank"><span className="icon-class"><FontAwesomeIcon icon={ faTwitterSquare }/></span></a>
            </div>
        )
    }
}
export default Social