                <ProjectWidecard title="Exploring Venues in Vadodara,Gujarat" description="Applied data science project exploring and giving insights on restaurants using Foursquare and Zomato API" from="Nov 2020" to="Nov 2020" link="https://github.com/AbdulBasit-MrRobo/Coursera_Capstone"/>
