import React, { Component } from 'react'

class Skills extends Component {
    constructor(props){
        super(props);
        this.state={
            'myskills':['Artificial Intelligence','Machine Learning','Blockchain','C','Java','HTML','CSS','JavaScript','Data Science','WordPress','Solidity','Python']
            //Agile/Scrum,ReactJS,Git
        };
    }
    render() {
        return (
            <div className="condiv skills">
                <h1 className="subtopic">My Skills</h1>
                <ul>
                {this.state.myskills.map((value)=>{
                return <li>{value}</li>
                })}
                </ul>
            </div>
        )
    }
}
export default Skills