import React, { Component } from 'react'

class ProjectWidecard extends Component {
    render() {
        return (
            <div className="widecard">
                <div className="compdet">
                    <h3>{this.props.title}</h3>
                    <span className="hidden">
                    <h4 className="secondtext">{this.props.description}</h4>
                    <h4 className="secondtext">{this.props.from} - {this.props.to}</h4>
                    <a href={this.props.link} target="_blank"><button className="btn btn2">Check it out</button></a>
                    </span>
                </div>
            </div>
        )
    }
}
export default ProjectWidecard